import React from "react"
import { Grid, Button, Image, Header } from "semantic-ui-react"
import querystring from "query-string"
import { labels } from "../../selections"
import GarageOptions from "../garageOptions"
import Menu from "../Menu"
import Design from "./Design"

const MobilePage = props => (
  <Design {...props}>
    {({
      selection,
      theme,
      color,
      floor,
      setTheme,
      setColor,
      setFloor,
      src,
    }) => (
      <Menu
        prev="/steps/1"
        next={`/steps/3?${querystring.stringify({
          selection,
          theme,
          color,
          floor,
        })}`}
        options={() => (
          <GarageOptions
            theme={theme}
            color={color}
            floor={floor}
            onSelect={options => {
              setTheme(options.theme)
              setColor(options.color)
              setFloor(options.floor)
            }}
          />
        )}
        content={toggle => (
          <Grid>
            <Grid.Row>
              <Grid.Column>
                <Header>{labels[selection]}</Header>
              </Grid.Column>
            </Grid.Row>
            <Grid.Row>
              <Grid.Column>
                <Image src={src} />
              </Grid.Column>
            </Grid.Row>
            <Grid.Row>
              <Grid.Column textAlign="center">
                <Button color="blue" onClick={toggle}>
                  Design
                </Button>
              </Grid.Column>
            </Grid.Row>
          </Grid>
        )}
      />
    )}
  </Design>
)

export default MobilePage
